export default {
	data() {
		return {
			payType: 1,
			orderInfo: {},
			timeId: '',
			modalName: null,
			coupons_id: -1,
			totalFee:0,
			selectTitle:'点击选择优惠券',
			amount:0
		};
	},
	computed: {

	},
	onLoad(options) {
		console.log(options)
		// options = {
		// 	orderId: "55"
		// }
		let _this = this;
		_this.getPageData(options);
	},

	methods: {
		calculateTotol(key){
			if(key != -1){
				let coupons = this.orderInfo.couponsList[key];
				this.totalFee = (this.orderInfo.price - coupons.reduced_price).toFixed(2);
				this.selectTitle = '选择:' + coupons.name + '  优惠金额:￥' + coupons.reduced_price
			}else{
				this.totalFee = (this.orderInfo.price * 1).toFixed(2)
				this.selectTitle = '请选择优惠券'
			}
		},
		selectCoupons(key){
			if(this.coupons_id == key){
				this.coupons_id = -1
			}else{
				this.coupons_id = key;
			}
			this.calculateTotol(this.coupons_id)
		},
		RadioChange(e) {
			this.radio = e.detail.value
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		checkPay(id) {
			var _this = this;
			let data = {
				'id': id
			};
			this.$http.post('/api/order/checkPay', data).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					if (data.paySuccess == true) {
						clearInterval(_this.timeId)
						uni.showToast({
							title: '支付成功！',
							icon: 'success'
						});
						//-ib8s8PRjp81McgQ6WJ0EGtHJn-nJ7lZuByZZv9nI-A

						setTimeout(function() {
							uni.navigateTo({
								url: '/pages/order/order?state=' + 2
							})
						}, 1000)
					}
				}
			}).catch(err => {
				console.log(err);
			})
		},
		getPageData(data) {
			var _this = this;
			uni.showLoading({
				title:'加载中...',
				mask:true
			})
			this.$http.post('/api/order/getOrder', data).then(res => {
				console.log(res)
				if (res.data.code != 1) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else {
					var data = res.data.data
					_this.orderInfo = data;
					_this.totalFee = _this.orderInfo.price;
					_this.amount = data.amount;
					uni.hideLoading()
					if(_this.orderInfo.couponsList != undefined && _this.orderInfo.couponsList.length != 0){
						let couponsList = _this.orderInfo.couponsList;
						_this.coupons_id = _this.get_object_first_attribute(couponsList);
						_this.calculateTotol(_this.coupons_id);
					}
				}
			}).catch(err => {
				console.log(err);
			})
		},
		get_object_first_attribute(data){
		    for (var key in data)
		        return key;
		},
		//选择支付方式
		changePayType(type) {
			this.payType = type;
		},
		//确认支付
		confirm: function() {
			var _this = this;
			if (_this.payType == 3 && _this.amount == 0) {
				uni.showToast({
					title: '余额不足',
					icon: 'none'
				});
				return false;
			}
			let data = {
				'order_id': _this.orderInfo.id,
				'pay_type': _this.payType
			}
			if(_this.coupons_id > -1){
				data.couponsRecordId = _this.orderInfo.couponsList[_this.coupons_id].id;
				data.reduced_price = _this.orderInfo.couponsList[_this.coupons_id].reduced_price;
			}
			this.$http.post('/api/order/doPay', data).then(res => {
				console.log(res.data.code == 19)
				if (res.data.code == 0) {
					uni.showToast({
						title: res.data.info,
						icon: 'none'
					});
				} else if(res.data.code == 19 || res.data.code == 29){ //0元或者 储值支付
					//订单支付成功
					_this.timeId = setInterval(function() {
						_this.checkPay(_this.orderInfo.id);
					}, 500)
				}else{
					var data = res.data.data
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: data.timeStamp,
						nonceStr: data.nonceStr,
						package: data.package,
						signType: data.signType,
						paySign: data.paySign,
						success: function(res) {
							_this.timeId = setInterval(function() {
								_this.checkPay(_this.orderInfo.id);
							}, 500)
						},
						fail: function(err) {
							console.log('fail:' + JSON.stringify(err));
						}
					});
				}
			}).catch(err => {
				console.log(err);
			})

		},
	}
}
